/* src/Components/BookingForm.css */
.form-container {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: rgba(0, 0, 0, 0.5);
	display: flex;
	justify-content: center;
	align-items: center;
	z-index: 1000;
	animation: fadeIn 0.5s;
}

.booking-form {
	background-color: #fff;
	padding: 20px;
	border-radius: 8px;
	box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);
	width: 300px;
	animation: slideIn 0.5s;
}

.booking-form h2 {
	color: #333;
	margin-bottom: 20px;
}

.booking-form input,
.booking-form select,
.booking-form textarea {
	width: 100%;
	padding: 10px;
	margin: 10px 0;
	border: 1px solid #ccc;
	border-radius: 4px;
	transition: border 0.3s;
}

.booking-form input:focus,
.booking-form select:focus,
.booking-form textarea:focus {
	border-color: #61dafb;
}

.booking-form button {
	background-color: #61dafb; /* Light blue background */
	color: white; /* White text */
	padding: 10px;
	border: none;
	border-radius: 4px;
	cursor: pointer;
	transition: background-color 0.3s, transform 0.2s; /* Add transition for hover effect */
}

.booking-form button:hover {
	background-color: #21a1f1; /* Darker blue on hover */
	transform: scale(1.05); /* Slightly scale up on hover */
}

.booking-form button:disabled {
	background-color: #ccc; /* Gray background for disabled button */
	cursor: not-allowed; /* Change cursor to indicate disabled */
}

@keyframes fadeIn {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}

@keyframes slideIn {
	from {
		transform: translateY(-20px); /* Start slightly above */
		opacity: 0; /* Start transparent */
	}
	to {
		transform: translateY(0); /* End at original position */
		opacity: 1; /* End fully visible */
	}
}