/* src/Components/Home.css */

.home {
	position: relative;
	height: 100vh; /* Full height of the viewport */
	overflow: hidden;
  }
  
  .home-image {
	position: relative;
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
  }
  
  .home-image img {
	width: 100%;
	height: 100%;
	object-fit: cover; /* Cover the entire container */
	filter: brightness(0.7); /* Darken the image slightly */
  }
  
  .overlay {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	color: white;
	text-align: center;
	z-index: 1; /* Ensure it appears above the image */
  }
  
  .overlay h2 {
	font-size: 3rem;
	margin-bottom: 10px;
  }
  
  .overlay p {
	font-size: 1.5rem;
	margin-bottom: 20px;
  }
  
  .book-now-button {
	background-color: #f1c40f; /* Bright yellow */
	color: #333; /* Dark text */
	padding: 15px 30px;
	text-decoration: none;
	border-radius: 5px;
	font-size: 1.2rem;
	transition: background-color 0.3s;
  }
  
  .book-now-button:hover {
	background-color: #e0b10f; /* Slightly darker yellow */
  }